                        {{key | upperCase }}
                    </td>
                    <td>
                        Total: {{user.balance.total[key]}} {{key | getCurrencySign}} Available: {{user.balance.total[key] - user.balance.blocked[key]}} {{key | getCurrencySign}} Blocked: {{user.balance.blocked[key]}} {{key | getCurrencySign}}
                    </td>
                    <td>
