<script>
    import iconEye from "@/assets/icons/eye.svg"
    import iconPencil from "@/assets/icons/pencil.svg"
    export default{
        name: "usersTable",
        data: function(){
          
            return {
                columns: ["id", "Full name", "Country", "Company", "Currency", "Balance", "State", "Preview", "Edit"],
                iconEye,
                iconPencil
            }
        },
        props: {
          users: Array
        },
        methods: {
          
        },
        computed: {
            filteredTransactions(){
                let transactions = this.transactions
                const userName = this.userName.firstname + " " + this.userName.lastname
                let tr = []
                if (userName){
                    transactions = transactions.filter(t => t.payer.clientName == userName)
                }
                return transactions
            }
        },
        filters: {
            upperCase(value){
                if (!value) return ''
                return value.toUpperCase()

            },
            dateFormat(value){
                if (!value) return ''
                value = value.slice(0, 10).replace(/-/g, ".")
                return value
            },
            idFormat(id){
                let a = id.toString()
                let b = ""
                for(let i=0; i<4-a.length; i++){
                    b+="0"
                }
                let position = 0
                let output = [a.slice(0, position), b, a.slice(position)].join('')
                return output
            },
            fixed(value){
                if (!value) return ''
                value = value.toFixed(2)
                return value
            },
            formatAccount(value){
                if (!value) return ''
                value.toString()
                value = "0" + value
                let output = value.substr(0, 3)
                output += " " + value.substr(3, 2)
                output += " " + value.substr(5, 3)
                output += " " + value.substr(8, 4)
                return output
            },
            getCurrencySign(currency){
                currency = currency.toString().substr(0, 4)
                const signs = {
                    "eur" : "€",
                    "gbp" : "£",
                    'usd' : "$",
                    "rub" : "₽"
                    }
                return signs[currency]
          },
            getStatusLabel(status){
              const labels = {
                  "approved": require("@/assets/table_approved.png"),
                  "rejected": require("@/assets/table_rejected.png"),

                }
              return labels[status] || "pending"
             }
        }
    }
</script>
