<script>
    // import tHistory from "../components/t-history.vue"
   
    import loader from "../components/loader"
    // import historyFilter from "../components/filters/history-filter"
    import tHistory from "../components/tables/t-history_a1.vue"
    import admin1Filter from "../components/filters/history_filter_1"
    import clientsTable from '../components/tables/clients'
    
    import {mapGetters, mapActions} from 'vuex'

    export default{
        name: "admin1",
        components: {
            loader,
            tHistory,
            admin1Filter,
            clientsTable,
        },
        data(){
            return{
                loading: true,
                currentTab: "tHistory",
                transactions: [],
                selectedUser: {
                    firstname: "Maria",
                    lastname: "Fedorova"
                    },
                filterParams: {
                    name: "",
                    dateFrom: new Date(),
                    dateTo: new Date(),
                    currency: "all",
					type: "all",
					state: "all"
                }
            }
        },
        props: {
            
        },
        methods:{
            setFilter(data) { 
                this.filterParams = data
            },
            ...mapActions(['fetchUsers', 'fetchTransactions']),
            getInfoFromLocal(){
                let user = JSON.parse(localStorage['user'])
                return user
            },
            logOut(){
                localStorage.clear()
                this.$router.push('/login')
            },
            
        },
        computed: {
            ...mapGetters(['allUsers', 'allTransactions']),
            currentTabComponent (){
                return this.currentTab
            },
        },
         async mounted(){
            await this.fetchTransactions().then(()=>{
               
            this.transactions = this.allTransactions
               this.loading = false
            }).catch(error => {
                this.err = error
                console.log(error)
            })
            await this.fetchUsers().then(()=>{
                
                this.user = this.allUsers[0]
            }).catch(error => {
                
                this.err = error
                console.log(error)
            })
        }
        
    }
</script>

