<template>
    <div class="wrapper_1">
         <table>
            <thead>
            <th>Date:</th>
            <th>Type:</th>
            <th>Currency:</th>
            <th>Amount:</th>
            <th>Payment description:</th>
            <th>Counterparty name:</th>
            <th>Counterparty bank account №:</th>
            <th>Status:</th>
            <th>Print:</th>
            <th>Preview:</th>
            </thead>
            <tbody>
           
            <tr v v-for="entry in filteredTransactions" :key="entry.id">
                    <td>{{entry.transactionDetails.valueDate | dateFormat}}</td>
                    <td>{{entry.type}}</td>
                    <td>{{entry.transactionDetails.currency | upperCase}}</td>
                    <td class="blue">{{entry.transactionDetails.amount | fixed}} {{entry.transactionDetails.currency | getCurrencySign}}</td>
                    <td>{{entry.paymentDescription.referenceMsg || "..."}}</td>
                    <td>{{entry.beneficiaryBank.name}}</td>
                    <td class="blue">{{entry.transactionDetails.bankAccount | formatAccount}}</td>
                    <td><img :src="entry.status | getStatusLabel" :alt="entry.status"></td>
                    <td><img src="../../assets/table_print.png" alt=""></td>
                    <td><img src="../../assets/icons/eye.svg" alt=""></td>
            </tr>
        
            </tbody>
        </table>
        
        <!-- <div class="pagination">
            <div class="arrow__prev-container">
                <i class="arrow__prev"></i>
                <span>Пред</span>
            </div>
            <span class="arrow__number active">1</span>
            <span class="arrow__number">2</span>
            <span class="arrow__other-page">...</span>
            <span class="arrow__number">20</span>
            <div class="arrow__next-container">
                <span>След</span>
