</template>

<script>
    
    export default{
        name: "t-history",
        data: function(){
            return{
                
            }
        },
        props: {
            transactions: Array,
            userName: Object,
            users: Array,
            filterParams: Object
        },
        methods: {
            getCurrencySign(currency){
                const signs = {
                    "eur" : "€",
                    "gbp" : "£",
                    'usd' : "$",
                    "rub" : "₽"
                    }
                let currencySign = signs[currency] 
                return currencySign
          },
        },
        computed: {
            filteredTransactions(){
                
                let transactions = this.transactions
                let f = this.filterParams    
                
                console.log("params: ", f)
               
                 if(f.name !== ""){
                    transactions = transactions.filter( t =>  
                    t.payer.clientName.toLowerCase() == f.name.toLowerCase(),
                   
                   )}  
                if(f.type !== "all"){
                    transactions = transactions.filter( t =>  
                    t.type == f.type
                   )}
                if (f.currency !=="all"){
                    transactions = transactions.filter( t =>  
                    t.transactionDetails.currency == f.currency
                    )}
                if (f.state !=="all"){
                    transactions = transactions.filter( t =>  
                    t.status == f.state
                    )}

                console.log(transactions)
                
                return transactions
            }
        },
        filters: {
            upperCase(value){
                if (!value) return ''
                return value.toUpperCase()

            },
            dateFormat(value){
                if (!value) return ''
                value = value.slice(0, 10).replace(/-/g, ".")
                return value
            },
            fixed(value){
                if (!value) return ''
                value = value.toFixed(2)
                return value
            },
            formatAccount(value){
                if (!value) return ''
                value.toString()
                value = "0" + value
                let output = value.substr(0, 3)
                output += " " + value.substr(3, 2)
                output += " " + value.substr(5, 3)
                output += " " + value.substr(8, 4)
                return output
            },
            getCurrencySign(currency){
                const signs = {
                    "eur" : "€",
                    "gbp" : "£",
                    'usd' : "$",
                    "rub" : "₽"
                    }
                let currencySign = signs[currency] 
                return currencySign
            },
            getStatusLabel(status){
              const labels = {
                "approved": require("@/assets/table_approved.png"),
                "rejected": require("@/assets/table_rejected.png"),
                "pending": require("@/assets/table_pending.png"),
                "executed": require("@/assets/table_executed.png")
                }
              return labels[status] || "pending"
             }
        }
    }
</script>
